import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { HttpService } from '../shared/http.service';
import Swal from 'sweetalert2';
import { ToastController } from '@ionic/angular';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-tab5',
  templateUrl: 'tab5.page.html',
  styleUrls: ['tab5.page.scss']
})
export class Tab5Page {

  constructor(private router: Router, private http: HttpService,
    private toastCtrl: ToastController, route: ActivatedRoute) {
    route.params.subscribe(val => {
      this.PopupModel = false;
      this.DisplayUserDetails();
     
     console.log( this.contact_number,  this.instagram, this.whatsapp,    this.website);



      

    });
  }


  ngOnInit() {

  }


  userdetails: any = JSON.parse(atob(localStorage.getItem("24hrs-user-data")));
  store_number: any;
  instagram: any;
  whatsapp: any;
  website: any;
  facebook: any;
  contact_number: any;
  store_address: any;
  youtube: any;
  delivery_availability_toggle: any;
  delivery_availability: any;
  InstagramToggle: any = " ";
  ContactNumberToggle: any;
  WhatsappToggle: any;
  WebsiteToggle: any;
  YoutubeToggle: any;
  FacebookToggle: any;
  WebsiteCheck: any;
  ContactNumberCheck: any;
  InstagramCheck: any;
  WhatsappCheck: any;
  YoutubeCheck: any;
  Facebookcheck: any;


  DisplayUserDetails() {
   
    const obj = {
      store_name: this.userdetails.storename
    }
    this.http.post('/seller_store_details', obj).subscribe((response: any) => {
    console.log(obj);
    
      if (response.success == "true") {
        console.log(response);
        this.store_number = response.records.store_number;
        this.contact_number = response.records.contact_number;
        this.instagram = response.records.instagram;
        this.whatsapp = response.records.whatsapp;
        this.website = response.records.website;
        this.facebook = response.records.facebook;
        this.store_address = response.records.store_address;
        this.youtube = response.records.youtube;
        this.delivery_availability = response.records.delivery_availability;

        if (this.instagram != "") {
          this.InstagramToggle = true;
          this.InstagramCheck = false;
        } else {
          this.InstagramToggle = false;
          this.InstagramCheck = true;
        }

        if (this.whatsapp != "") {
          this.WhatsappToggle = true;
          this.WhatsappCheck = false;
        } else {
          this.WhatsappToggle = false;
          this.WhatsappCheck = true;
        }
      }

      if (this.delivery_availability == true) {
        this.delivery_availability_toggle = true;
      } else {
        this.delivery_availability_toggle = false;
      }

      if (this.contact_number != "") {
        this.ContactNumberToggle = true;
        this.ContactNumberCheck = false;

      } else {
        this.ContactNumberToggle = false;
        this.ContactNumberCheck = true;
      }

      if (this.website != "") {
        this.WebsiteToggle = true;
        this.WebsiteCheck = false;

      } else {
        this.WebsiteToggle = false;
        this.WebsiteCheck = true;

      }

      if (this.youtube != "") {
        this.YoutubeToggle = true;
        this.YoutubeCheck = false;
      } else {
        this.YoutubeToggle = false;
        this.YoutubeCheck = true;
      }

      if (this.facebook != "") {
        this.FacebookToggle = true;
        this.Facebookcheck = false;
      } else {
        this.FacebookToggle = false;
        this.Facebookcheck = true;
      }
     


    }, (error: any) => {
      console.log(error);
    }
    );
  }


  Deliveryavailability: any = true;

  deliveryStatus(event) {

    console.log(event);

    this.Deliveryavailability = !this.Deliveryavailability;

    console.log(this.Deliveryavailability);
    if (this.Deliveryavailability == true || this.Deliveryavailability == null) {
      this.delivery_availability = false
    } else {
      this.delivery_availability = true
    }


  }

  PopupModel: any = false;

  userDetailstVisible: any = true;
  contactVisible: any = true;

  support() {
    this.router.navigate(['/support'])
  }

  backToprivious() {
    this.PopupModel = false;

  }
  popupModelOpen() {
    this.PopupModel = true;
    this.userDetailstVisible = true;
    this.contactVisible = false;
  }

  contactEdit() {
    this.PopupModel = true;
    this.contactVisible = true;
    this.userDetailstVisible = false;
  }


  userdetail: any = atob(localStorage.getItem("24hrs-user-data"));

  parsedObj: any = JSON.parse(this.userdetail);
  updateProfile() {

    const contactData = {
      tbid: this.parsedObj.id,
      website: this.website,
      whatsapp: this.whatsapp,
      instagram: this.instagram,
      facebook: this.facebook,
      youtube: this.youtube,
      contact_number: this.contact_number,
      delivery_availability: this.delivery_availability

    }

    console.log(contactData);

    this.http.post('/seller_contact', contactData).subscribe((response: any) => {
      if (response.success == "true") {
        console.log(response);
        this.DisplayUserDetails();
        const Toast = Swal.mixin({
          toast: true,
          position: 'top-end',
          showConfirmButton: false,
          timer: 1000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener('mouseenter', Swal.stopTimer)
            toast.addEventListener('mouseleave', Swal.resumeTimer)
          }
        })

        Toast.fire({
          icon: 'success',
          title: 'updated successfully'
        })
        this.router.navigate(['/tabs'])
      } this.backToprivious()
      this.router.navigate(['/tabs/tab5'])

    }, (error: any) => {
      console.log(error);
    }
    );
  }

}
